.section-reset {
  margin-bottom: 60px;

  &__form  {
    max-width: 400px;
    min-width: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: calc(80vh - 60px);

    form {
      width: 100%;
    }

    h1 {
      font-size: 20px;
      margin-bottom: 60px;
      text-align: center;
      font-weight: normal;

      small {
        display: block;
      }
    }
  }
}